<template>
  <v-container grid-list-xs>
    <v-overlay :value="overlay">
      <v-progress-circular
          color="primary"
          indeterminate
          size="50"
      ></v-progress-circular>
      <v-btn icon @click="overlay = false">
        <v-icon>mdi-close</v-icon>
      </v-btn>
    </v-overlay>
    <v-navigation-drawer
      app
      class="primary darken-1 white--text"
      clipped
      permanent
      right
      style="z-index: 3;"
    >
      <v-list class="white--text">
        <v-list-item>
          <v-btn class="secondary" dark @click="$router.push('/resource/view/bwpractitioner/' + queries.practitioner)">
            <v-icon light left>mdi-eye</v-icon>
            <span>View Health Profession</span>
          </v-btn>
        </v-list-item>
      </v-list>
      <v-list class="white--text">
        <v-list-item>
          <v-btn class="secondary" dark @click="$router.go(-1)">
            <v-icon light left>mdi-eye</v-icon>
            <span>Go back</span>
          </v-btn>
        </v-list-item>
      </v-list>
    </v-navigation-drawer>
    <v-card
      class="mx-auto"
      max-width="700"
      outlined
    >
      <v-card-title class=" justify-center darken-1 primary white--text text-uppercase font-weight-bold">
        License Renewal Application
      </v-card-title>
      <v-card-text class="my-3">
        <v-menu
          v-model="applicationDateMenu"
          :close-on-content-click="false"
          :nudge-right="40"
          transition="scale-transition"
          offset-y
          min-width="290px"
        >
          <template v-slot:activator="{ on }">
            <v-text-field
              :disabled="hasPendingApplication"
              v-model="applicationDateFormatted"
              label="Application Date*"
              prepend-inner-icon="mdi-calendar"
              readonly
              v-on="on"
              :rules="applicationdaterules"
              outlined
              hide-details="auto"
              dense
            />
          </template>
          <v-date-picker
            color="secondary"
            :landscape="$vuetify.breakpoint.smAndUp"
            v-model="applicationDate"
            @change="applicationDateFormatted = formatDate(applicationDate)"
            @input="applicationDateMenu = false"
          />
        </v-menu>
        Select registration numbers to renew
        <v-checkbox
          :disabled="hasPendingApplication"
          dense
          v-for="regNum in registrations"
          :key="regNum.id"
          v-model="selectedRegs"
          :label="regNum.text"
          :value="regNum.id"
        ></v-checkbox>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer><v-btn color="success" :disabled="!canSubmit" @click="apply">Apply</v-btn>
      </v-card-actions>
    </v-card>
  </v-container>
</template>
<script>
const querystring = require("querystring");
export default {
  props: ["queries"],
  data() {
    return {
      overlay: false,
      registrations: [],
      selectedRegs: [],
      applicationDate: '',
      applicationDateFormatted: '',
      applicationDateMenu: false,
      errors: {
        applicationdate: []
      },
      hasPendingApplication: false
    }
  },
  computed: {
    applicationdaterules() {
      return [
        (v) => {
          this.errors.applicationdate = []
          if(!v) {
            this.errors.applicationdate = ["Application date is required"]
            return "Application date is required"
          }
          return true
        }
      ]
    },
    canSubmit() {
      if(this.selectedRegs.length > 0 && this.applicationDate) {
        return true
      }
      return false
    }
  },
  methods: {
    formatDate(date) {
      if (!date) return null;
      const [year, month, day] = date.split("-");
      return `${day}-${month}-${year}`;
    },
    checkPendingApplication() {
      let params = {
        '_profile': 'http://ihris.org/fhir/StructureDefinition/nmcb-license-renewal-application-profile',
        'practitioner': "Practitioner/" + this.queries.practitioner,
        'nmcbrenewalstatus:not': "renewalapproved,renewalrejected"
      }
      fetch('/fhir/Basic?' + querystring.stringify(params)).then((response) => {
        response.json().then((pending) => {
          if(pending.entry && pending.entry.length > 0) {
            this.$store.commit("setMessage", {
              type: "error",
              text: "You have a pending license renewal application."
            });
            this.hasPendingApplication = true
          }
        })
      })
    },
    getRegistrations() {
      let params = {
        '_profile': 'http://ihris.org/fhir/StructureDefinition/nmcb-registration-profile',
        practitioner: 'Practitioner/' + this.queries.practitioner
      }
      fetch('/fhir/Basic?' + querystring.stringify(params)).then((response) => {
        response.json().then((registrations) => {
          if(!registrations.entry || registrations.entry.length === 0) {
            return
          }
          for(let entry of registrations.entry) {
            let regNum = entry.resource.extension.find((ext) => {
              return ext.url === 'http://ihris.org/fhir/StructureDefinition/registration-number'
            })
            if(regNum) {
              this.registrations.push({
                text: regNum.valueString,
                id: entry.resource.id
              })
            }
          }
        })
      })
    },
    apply() {
      let opts = {
        method: "POST",
        headers: {
          "Content-Type": "application/fhir+json"
        },
        redirect: 'manual',
        body: JSON.stringify({
          registrations: this.selectedRegs,
          applicationDate: this.applicationDate,
          practitioner: this.queries.practitioner
        })
      }
      this.overlay = true
      fetch("/site/nmcb/licenseRenewal", opts).then((response) => {
        this.overlay = false;
        if(response.status === 201) {
          this.$store.commit("setMessage", {
            type: "success",
            text: "License Renewal Application Submitted",
          });
        } else {
          this.$store.commit("setMessage", {
            type: "error",
            text: "Failed To Submit License Renewal."
          });
        }
        this.$router.go(-1)
      }).catch(() => {
        this.overlay = false;
        this.$store.commit("setMessage", {
          type: "error",
          text: "Failed To Submit License Renewal."
        });
      })
    }
  },
  created() {
    this.checkPendingApplication()
    this.getRegistrations()
  }
}
</script>